import React from 'react';
import PropTypes from 'prop-types';
import { observer, inject } from 'mobx-react';
import CreatableSelect from 'react-select/lib/Creatable';
import socket from '../../api/socket';
import { sendFormData } from '../../api/request';
import defaultAvatar from '../../utils/defaultAvatar';

const titles = [
  'O19',
  'O18',
  'O17',
  'O16',
  'O15',
  'O14',
  'O13',
  'O12',
  'O11',
  'O10',
  'O9',
  'O8',
  'O7',
  'O6',
  'O5',
  'O3',
  'O2',
  'O1',
  'O+',
];

@inject('memberStore')
@observer
class InputMember extends React.Component {
  constructor(props) {
    super(props);
    this.id = null;
    this.name = '';
    this.state = {
      title: 'O17',
      image: '',
      attend: false,
      payment: false,
      imageMode: 'upload',
    };
  }

  handleChange = (newValue, actionMeta) => {
    if (!newValue) {
      return;
    }
    if (actionMeta.action === 'create-option') {
      this.id = Date.now().toString();
      this.setState({
        image: '',
      });
    } else {
      this.id = newValue.value;
      const { memberStore } = this.props;
      const member = memberStore.members.get(this.id);
      this.setState({
        image: member.image,
        payment: member.payment,
        attend: member.attend,
        title: member.title,
      });
    }
    this.name = newValue.label;
  };

  addMember = () => {
    const newMember = {
      id: this.id,
      name: this.name,
      ...this.state,
    };
    console.log('new member', newMember);
    socket.emit('add-member', newMember);
  };

  onImageSelected = async (e) => {
    try {
      const { files } = e.target;
      const file = files[0];
      const res = await sendFormData('/upload', { image: file });
      this.setState({ image: res.image });
    } catch (ex) {
      console.log(ex);
    }
  };

  setUIAvatar = name => (e) => {
    e.target.src = defaultAvatar(name);
  };

  changeImageMode = imageMode => () => {
    this.setState({ imageMode });
  };

  render() {
    const { memberStore } = this.props;
    const members = memberStore.members.array;
    const { countMember, countAttendMember, countPaymentMember } = memberStore;
    const {
      title, image, payment, attend, imageMode,
    } = this.state;
    const options = memberStore.members.map(m => ({ value: m.id, label: m.name }));
    return (
      <div className="container input-user">
        <div className="row">
          <div className="left col-md">
            <div className="form-group">
              Tên người tham dự
              <CreatableSelect
                isClearable
                onChange={this.handleChange}
                placeholder="Tên người tham gia"
                noOptionsMessagefunction="Chưa có ai tham gia"
                options={options}
              />
            </div>
            <div className="form-group">
              O
              <select
                className="form-control"
                value={title}
                onChange={e => this.setState({ title: e.target.value })}
              >
                {titles.map(t => (
                  <option key={t} value={t}>
                    {t}
                  </option>
                ))}
              </select>
            </div>
            <div className="d-flex">
              <label>
                <input type="radio"
                  checked={imageMode === 'upload'}
                  onClick={this.changeImageMode('upload')}
                />
                <span className="mx-1">Upload file</span>
              </label>
              <label>
                <input type="radio"
                  checked={imageMode === 'link'}
                  onClick={this.changeImageMode('link')}
                />
                <span className="mx-1">Link hình</span>
              </label>
            </div>
            {imageMode === 'link' ? (
              <div className="form-group">
                Link ảnh
                <input
                  type="text"
                  className="form-control"
                  placeholder="Link ảnh"
                  value={image}
                  onChange={e => this.setState({ image: e.target.value })}
                />
              </div>
            ) : (
              <div className="form-group">
                <label className="btn btn-success mt-3">
                  Upload hình
                  <input
                    type="file"
                    accept="image/*"
                    className="d-none"
                    onChange={this.onImageSelected}
                  />
                </label>
              </div>
            )}
            <div className="d-flex">
              <label className="flex-fill">
                <input
                  type="checkbox"
                  name="attend"
                  className="mr-2"
                  checked={attend}
                  onChange={e => this.setState({ attend: e.target.checked })}
                />
                Tham gia
              </label>
              <label className="flex-fill">
                <input
                  type="checkbox"
                  name="attend"
                  className="mr-2"
                  checked={payment}
                  onChange={e => this.setState({ payment: e.target.checked })}
                />
                Đã đóng tiền
              </label>
            </div>
            <div className="my-2">
              <button type="button" className="btn btn-primary" onClick={this.addMember}>
                Thêm
              </button>
            </div>
            {image ? (
              <div className="input-avatar">
                <img src={image} alt="Avatar" onError={this.setUIAvatar(this.name)} />
              </div>
            ) : null}
          </div>
          <div className="right col-md">
            <div className="title text-center border-bottom mb-2">
              Danh sách người tham dự ({countAttendMember}/{countMember}) - {countPaymentMember} đã
              đóng tiền
            </div>
            <div className="member-list">
              {members.map(m => (
                <div className="member" key={m.id}>
                  <div className="info">
                    <img
                      className="avatar"
                      src={m.image}
                      alt={m.name}
                      onError={this.setUIAvatar(m.name)}
                    />
                    <span>{m.name}</span> - {m.title}
                  </div>
                  <div className="status">
                    <img
                      className={`attend ${m.attend ? 'done' : ''}`}
                      src="/images/attend.png"
                      alt="attend"
                    />
                    <img
                      className={`payment ${m.payment ? 'done' : ''}`}
                      src="/images/finance.png"
                      alt="payment"
                    />
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

InputMember.propTypes = {
  memberStore: PropTypes.instanceOf(PropTypes.object),
};

export default InputMember;
