import React from 'react';
import PropTypes from 'prop-types';
import { Animated } from 'react-animated-css';

const Person = ({ member }) => (
  <div className="person">
    <Animated
      isVisible={!!member}
      animationIn="tada"
      animationOut="bounceOut"
      className="avatar"
      style={member ? { backgroundImage: `url(${member.image})` } : null}
    />
    <div className="info">
      <div className="name">{member ? member.name : '...'}</div>
      <div className="title">{member ? member.title : ''}</div>
    </div>
  </div>
);

// const Person = ({ member }) => ;

Person.propTypes = {
  member: PropTypes.instanceOf(PropTypes.object),
};
export default Person;
