import React from 'react';
import PropTypes from 'prop-types';
import { observer, inject } from 'mobx-react';
import getAnimation from '../../utils/animations';
import defaultAvatar from '../../utils/defaultAvatar';

@inject('memberStore')
@observer
class Main extends React.Component {

  setUIAvatar = name => (e) => {
    e.target.src = defaultAvatar(name);
  };

  render() {
    const { memberStore } = this.props;
    return (
      <div className="main">
        {memberStore.members.map(m => (
          <div className="member-avatar" key={m.id}>
            <img className={`animated infinite delay-2s ${getAnimation()}`} src={m.image} alt={m.name} onError={this.setUIAvatar(m.name)} />
          </div>
        ))}
      </div>
    );
  }
}
Main.propTypes = {
  memberStore: PropTypes.instanceOf(PropTypes.object),
};

export default Main;
