import React from 'react';
import PropTypes from 'prop-types';
import { observer, inject } from 'mobx-react';
import Slider from 'react-slick';
import defaultAvatar from '../../utils/defaultAvatar';

const settings = {
  className: 'center',
  centerMode: true,
  infinite: true,
  centerPadding: '60px',
  slidesToShow: 3,
  slidesToScroll: 1,
  autoplay: true,
  autoplaySpeed: 2000,
  cssEase: 'linear',
};

@inject('memberStore')
@observer
class Welcome extends React.Component {
  setUIAvatar = name => (e) => {
    e.target.src = defaultAvatar(name);
  };

  render() {
    const { memberStore } = this.props;
    return (
      <div className="welcome">
        <Slider {...settings}>
          {memberStore.members.array.map(m => (
            <div key={m.id} className="welcome-member">
              <img src={m.image} className="avatar" alt="" />
              {/* <h3 className="name">{m.name}</h3> */}
            </div>
          ))}
        </Slider>
      </div>
    );
  }
}
Welcome.propTypes = {
  memberStore: PropTypes.instanceOf(PropTypes.object),
};

export default Welcome;
