import { observable, computed, action } from 'mobx';
import List from '../utils/list';

class MemberStore {
  @observable
  members = new List([]);

  @computed
  get countMember() {
    return this.members.length;
  }

  @computed
  get countAttendMember() {
    return this.members.array.filter(m => m.attend).length;
  }

  @computed
  get countPaymentMember() {
    return this.members.array.filter(m => m.payment).length;
  }

  @action.bound
  setMembers(members) {
    this.members = members;
  }
}

export default new MemberStore();
