import React from 'react';
import PropTypes from 'prop-types';
import { observer, inject } from 'mobx-react';
import { toJS } from 'mobx';
import Person from '../shared-components/person';

const defaultPerson = {
  image: '/images/unknownperson.png',
  name: 'Unknown',
  title: '',
  isDefault: true,
};

@inject('memberStore')
@observer
class GiftExchange extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      step: 1,
      member1: defaultPerson,
      member2: defaultPerson,
    };
  }

  getAPerson() {
    const personCount = this.members.length;
    if (!personCount) {
      return null;
    }
    const random = Math.floor(Math.random() * personCount);
    const member = this.members[random];
    this.members.splice(random, 1);
    return member;
  }

  getAPair() {
    if (!this.members.length) {
      this.setState({ step: 3 });
    }
    const member1 = this.getAPerson();
    const member2 = this.getAPerson();
    console.group('Pair');
    console.log('member1', member1);
    console.log('member2', member2);
    console.groupEnd();
    this.setState({ member1: null, member2: null });
    setTimeout(() => {
      this.setState({ member1, member2 });
    }, 300);
  }

  start = () => {
    const { memberStore } = this.props;
    this.setState({ step: 2 });
    const isDemo = window.location.pathname === '/random-demo';
    this.members = toJS(memberStore.members.array).filter(m => isDemo || m.attend);
  };

  action = step => () => {
    if (step === 1) {
      this.start();
    }
    this.getAPair();
  };

  renderBtn() {
    const { step } = this.state;
    const text = step === 1 ? 'Bắt đầu' : step === 2 ? 'Tiếp tục' : 'Kết thúc';
    return (
      <button className="btn btn-primary start-btn" onClick={this.action(step)}>
        {text}
      </button>
    );
  }

  render() {
    const { member1, member2, step } = this.state;
    return (
      <div className="container gift-exchange">
        {step < 3 ? (
          <React.Fragment>
            <div className="pair">
              <Person member={member1} />
              <img
                className="exchange animated swing infinite"
                src="/images/gift-exchange.png"
                alt="exchange"
              />
              <Person member={member2} />
            </div>
            {this.renderBtn()}
          </React.Fragment>
        ) : null}
        <div className="remain">
          {this.members && this.members.length}
        </div>
      </div>
    );
  }
}

GiftExchange.propTypes = {
  memberStore: PropTypes.instanceOf(PropTypes.object),
};

export default GiftExchange;
